export const nombreAleatorio = (numeroAleatorio) => {
  let nom = '';

  switch (numeroAleatorio) {
    case 0:
      nom = 'Sandro';
      break;
    case 1:
      nom = 'Rafa';
      break;
    case 2:
      nom = 'Verzo';
      break;
    case 3:
      nom = 'Laura';
      break;
    case 4:
      nom = 'Goyo';
      break;
    default:
      nom = 'Sandro';
      break;
  }

  return nom;
};

